            </van-nav-bar>
        </div>

        <div class="content-top">
            <van-tabs @click="onClick">
                <van-tab title="正在热映">
                    <div class="movie-box">
                        <div class="content-box clearfix" v-for="(item,index) in nowMovieData.subjects" :key="index" @click="movieDetail({name: 'movieDetail', params: {id: item.id}})">
