                                <img class="auto-img" :src="item.images.medium" alt="">
                            </div>

                            <div class="content fl">
                                <div class="title movie-name">{{item.title}}</div>
                                <div class="title movie-people">主演:{{item.avatarsInfo}}</div>
                                <div class="title movie-type">类别:{{item.genresInfo}}</div>
                                <div class="title movie-time">时间:{{item.durations[0]}}</div>
                            </div>

                            <div class="buy-info fl">
                                <div class="number">{{item.rating.average}}分</div>
                            </div>
                        </div>
                    </div>
                </van-tab>

                <van-tab title="即将上映">
