        created(){

            //请求数据
            this.axios({
                //请求类型
                method: "GET",
                //请求地址
                url: "https://douban.uieee.com/v2/movie/in_theaters?start=0&count=50&city=广州",
                //请求参数
                params: this.params
            }).then(result => {
                // console.log('result ==>',result)
                result.data.subjects.forEach(v => {
                    //合并电影类型
                    v.genresInfo = v.genres.join(" / ");

                    //合并演员
                    v.avatarsInfo = "";
                    v.casts.forEach(item => {
                        v.avatarsInfo += item.name + " / ";
                    })
                });

                this.nowMovieData = result.data;
