<style lang="less" scoped>
.movie{
    position: fixed;
    top: 0;
    right: 0;
    bottom: 50px;
    left: 0;
    
    .van-tabs {
        position: static;
    }


    .movie-box{    
        position: absolute;
        top: 90px;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: #f5f5f5;
        overflow-y: auto;

